import { Model } from './base/Model';
export class Product extends Model {
}
/*
  * Класс, описывающий состояние приложения
  * */
export class AppState extends Model {
    constructor() {
        super(...arguments);
        // Корзина с товарами
        this.basket = [];
        // Объект заказа клиента
        this.order = {
            items: [],
            payment: '',
            total: null,
            address: '',
            email: '',
            phone: '',
        };
        // Объект с ошибками форм
        this.formErrors = {};
    }
    addToBasket(value) {
        this.basket.push(value);
    }
    deleteFromBasket(id) {
        this.basket = this.basket.filter(item => item.id !== id);
    }
    clearBasket() {
        this.basket.length = 0;
    }
    getBasketAmount() {
        return this.basket.length;
    }
    setItems() {
        this.order.items = this.basket.map(item => item.id);
    }
    setOrderField(field, value) {
        this.order[field] = value;
        if (this.validateContacts()) {
            this.events.emit('contacts:ready', this.order);
        }
        if (this.validateOrder()) {
            this.events.emit('order:ready', this.order);
        }
    }
    validateContacts() {
        const errors = {};
        if (!this.order.email) {
            errors.email = 'Необходимо указать email';
        }
        if (!this.order.phone) {
            errors.phone = 'Необходимо указать телефон';
        }
        this.formErrors = errors;
        this.events.emit('contactsFormErrors:change', this.formErrors);
        return Object.keys(errors).length === 0;
    }
    validateOrder() {
        const errors = {};
        if (!this.order.address) {
            errors.address = 'Необходимо указать адрес';
        }
        if (!this.order.payment) {
            errors.payment = 'Необходимо указать способ оплаты';
        }
        this.formErrors = errors;
        this.events.emit('orderFormErrors:change', this.formErrors);
        return Object.keys(errors).length === 0;
    }
    refreshOrder() {
        this.order = {
            items: [],
            total: null,
            address: '',
            email: '',
            phone: '',
            payment: ''
        };
    }
    getTotalBasketPrice() {
        return this.basket.reduce((sum, next) => sum + next.price, 0);
    }
    setStore(items) {
        this.store = items.map((item) => new Product(Object.assign(Object.assign({}, item), { selected: false }), this.events));
        this.emitChanges('items:changed', { store: this.store });
    }
    resetSelected() {
        this.store.forEach(item => item.selected = false);
    }
}
