import { Component } from './base/Component';
import { ensureElement } from '../utils/utils';
/*
  * Класс, описывающий главную страницу
  * */
export class Page extends Component {
    // Конструктор принимает родительский элемент и обработчик событий
    constructor(container, events) {
        super(container);
        this.events = events;
        this._counter = ensureElement('.header__basket-counter');
        this._store = ensureElement('.gallery');
        this._wrapper = ensureElement('.page__wrapper');
        this._basket = ensureElement('.header__basket');
        this._basket.addEventListener('click', () => {
            this.events.emit('basket:open');
        });
    }
    // Сеттер для счётчика товаров в корзине
    set counter(value) {
        this.setText(this._counter, String(value));
    }
    // Сеттер для карточек товаров на странице
    set store(items) {
        this._store.replaceChildren(...items);
    }
    // Сеттер для блока прокрутки
    set locked(value) {
        if (value) {
            this._wrapper.classList.add('page__wrapper_locked');
        }
        else {
            this._wrapper.classList.remove('page__wrapper_locked');
        }
    }
}
